/* =============================================================================
 * Links
============================================================================= */
a {
	color:var(--color-black);
	text-decoration:underline;
	transition:color 0.15s ease;
}

a:visited {
	color:var(--color-black);
}

a:hover,
a:focus {
	color:var(--color-black);
	text-decoration:none;
}
