:root {
	--o-section-spacing-xl: 80px;
	--o-section-spacing-lg: 80px;
	--o-section-spacing-md: 40px;
}

.o-section {
  margin-bottom: var(--o-section-spacing-md);
}

.o-section__headline {
	margin-bottom: 60px;
	text-align: center;
}

@media(--md) {
	.o-section {
	  margin-bottom: var(--o-section-spacing-md);
	}}

@media(--lg) {
	.o-section {
	  margin-bottom: var(--o-section-spacing-lg);
	}
}

@media(--xl) {
	.o-section {
	  margin-bottom: var(--o-section-spacing-xl);
	}
}
