/* Nav supp
-------------------------------------------------------------- */
.c-supplemental-navigation {
	position: relative;
	z-index: 10;
	padding: 1px 0;
	/*-webkit-box-shadow: rgba(255, 255, 255, 0.13) 1px 1px 0;
	box-shadow: rgba(255, 255, 255, 0.13) 1px 1px 0;*/
	background: #000;
}

.c-supplemental-navigation__inner {
	display: flex;
	justify-content: space-between;
}

.c-supplemental-navigation__secondary {
	display: flex;
}

.c-supplemental-navigation__link > a,
.c-supplemental-navigation__link > a:hover,
.c-supplemental-navigation__link > a:visited {
	display: block;
	color: inherit;
	text-decoration: inherit;
}

/* [class] is added to win over o-list-inline. */
.c-supplemental-navigation__link[class] {
	position: relative;
	padding: 4px 12px 5px;
	color: #fff;
	font-weight: 600;
	font-size: 13px;
	font-family: 'Oswald', 'arial narrow', sans-serif;
	text-decoration: none;
}

.c-supplemental-navigation__link a {
	font-weight: inherit;
	font-family: inherit;
}

.c-supplemental-navigation__link:hover {
	color: var(--color-white);
	text-decoration: underline;
}

.c-supplemental-navigation__link--highlighted {
	background: #e81b23;
	background: linear-gradient(to bottom, #e81b23, #ba1218);

}

@media (max-width:500px) {
	.c-supplemental-navigation__inner {
		justify-content: flex-end;
	}

	.c-supplemental-navigation__secondary {
		display: none;
	}

	.c-supplemental-navigation__link {
		color: #b8b8b8;
		font-size: 13px;
	}

	.c-supplemental-navigation__link > a {
		padding: 11px 12px 10px 12px
	}
}
