/* =============================================================================
 * Main navigation
 * postcss-bem-linter: define main-navigation
 *
 * 1. Border bottom. Since it is below the white BG, this seemed easier.
============================================================================= */
.c-main-navigation {
	color:#fff;
}

.c-main-navigation__link {
	display:block;
	position: relative;
	padding:10px 20px;
	color:inherit;
	text-decoration: none;
}

.c-main-navigation__link:hover,
.c-main-navigation__link:visited,
.c-main-navigation__link:active {
	color: inherit
}

.c-main-navigation__link::after {
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	height: 2px;
	background-color: rgba(255, 255, 255, 0);
	content: '';
	transition: all 200ms ease-out;
}

.c-main-navigation__link:hover::after {
	background-color: rgba(255, 255, 255, 1);
}

.c-main-navigation__link--selected {
	color:#fff;
	font-weight: bold;
}



.c-mobile-navigation {
	position: relative;
}

.c-mobile-navigation__toggle-button {
	position: relative;
	width: 32px;
	height: 32px;
	padding: 0;
	border: none;
	background: transparent;
}

.c-mobile-navigation__toggle-button span,
.c-mobile-navigation__toggle-button::before,
.c-mobile-navigation__toggle-button::after {
	position: absolute;
	left: 4px;
	width: 24px;
	height: 2px;
	background: #fff;
	content: '';
}

.c-mobile-navigation__toggle-button span {
	top: 15px;
	text-indent: -9999px;
}

.c-mobile-navigation__toggle-button::before {
	top: 7px;
}

.c-mobile-navigation__toggle-button::after {
	bottom: 7px;
}

.c-mobile-navigation__list {
	display: none;
	position: absolute;
	top: 100%;
	right: 0;
	padding: 0 8px;
	border-radius: 3px 0 3px 3px;
	background: #fff;
}

.c-mobile-navigation__item {
	border-bottom: 1px solid #ddd;
}

.c-mobile-navigation__item:last-child {
	border-bottom: none;
}

.c-mobile-navigation__link {
	display: block;
	padding: 10px;
	text-decoration: none;
	white-space: nowrap;
}

.c-mobile-navigation--expanded .c-mobile-navigation__list {
	display: block;
}
