/* =============================================================================
 * Footer
 * postcss-bem-linter: define footer
============================================================================= */

.c-footer {
	position: relative;
	margin-top: 70px;
	padding: 15px 20px;
	background: #000;
	color: #fff;
	font-size: 13px;
}

.c-footer__inner {
}

.c-footer .c-footer__logo {
	flex-grow: 0;
	flex-shrink: 1;
}

.c-footer .c-footer__logo img {
	height: auto;
}

.c-footer .c-footer__address {
	line-height: 16px;
}

.c-footer .c-footer__menu {
	line-height: 20px;
}

.c-footer h2 {
	margin-bottom: 17px;
	color: #fff;
	font-weight: 700;
	font-size: 18px;
	font-family: 'Oswald', 'arial narrow', sans-serif;
}

.c-footer-search {
	position: relative;
}

.c-footer a {
	color: #fff;
	font-size: 13px;
	text-decoration: none;
}

.c-footer a:hover,
.c-footer a:active,
.c-footer a:focus {
	text-decoration: underline;
}

.c-footer__nav-supp {
	margin: 27px 0 0 94px;
	font-size: 13px;
}

.c-footer__nav-supp > li {
	margin-right: 20px;
}

.c-footer__nav-supp > li:last-child {
	margin-right: 0;
}

.c-footer .c-footer__nav-supp li:first-child {
	padding-left: 0;
	background: none;
}

.c-footer__menu,
.c-footer__nav-supp {
	display: none;
}

.c-footer__logo {
	width: 70px;
}

.c-footer__address > h2 {
	display: none;
}

.c-footer__address,
.c-footer__copy,
.c-footer__copy p {
	margin-bottom: var(--spacing-1);
}

@media (--md) {
	.c-footer__menu,
	.c-footer__nav-supp {
		display: block;
	}
}

@media (--md) {
	.c-footer {
		padding: 42px 0 70px;
	}

	.c-footer__logo {
		width: auto;
	}

	.c-footer__address > h2 {
		display: block;
	}
}
