/* =============================================================================
 * Width limiter
 *
 * Limits width and centers container on page.
============================================================================= */
.o-width-limiter {
	max-width:1080px;
	margin-right:auto;
	margin-left:auto;
	padding-right:5%;
	padding-left:5%;
}

.o-width-limiter--small {
	max-width:820px;
}

.o-width-limiter--large {
	max-width:1500px;
}

@media(--md) {
	.o-width-limiter {
		
	}
}

@media(--lg) {
	.o-width-limiter {
		padding-right:2%;
		padding-left:2%;
	}
}