.c-teaser {
  margin-bottom: var(--spacing-6);
}

.c-teaser__media {
  width: 100%;
  margin-bottom: var(--spacing-2);
  text-align: center;
}

.c-teaser__header {
  display: flex;
  align-items: flex-end;
}

.c-teaser__header h2 {
  flex-grow: 1;
  flex-shrink: 1;
}

.c-teaser__header a,
.c-teaser__header a:hover
.c-teaser__header a:visited {
  color: inherit;
  text-decoration: none;
}

.c-teaser__header a:hover {
  text-decoration: underline;
}


.c-teaser__icon {
  display: block;
  flex-grow: 0;
  flex-shrink: 1;
  max-width: 64px;
  width: 100%;
  height: 64px;
  margin-right: 10px;
}
