.c-quote[class] {
	margin-bottom:60px;
	text-align:center;
}

.c-quote::before {
	display:inline-block;
	width:46px;
	height:35px;
	overflow:hidden;
	margin-bottom:var(--spacing-1);
	background:transparent url(../../images/icons/quote.svg) no-repeat 0 0;
	content:"";
}

.c-quote__body {
	font-style:italic;
}

.c-quote__author-name {
	display:block;
	color:#045389;
}

@media(--md) {
	.c-quote[class] {
		margin-bottom:24px;
	}
}