/* =============================================================================
 * Grid
============================================================================= */
:root {
	--grid-gutter:30px;
	--vspace:0;
}
.o-grid {
	display:flex;
	flex-wrap:wrap;
	margin:0 calc(var(--grid-gutter) * -0.5);
}
.o-grid > [data-size] {
	display:flex;
	flex-direction:column;
	margin-bottom:var(--vspace);
	padding:0 calc(var(--grid-gutter) * 0.5);
}


/* Collapsed
 * No margin or padding
----------------------------------------------------------------------------- */
.o-grid--collapsed {
	margin:0;
}
.o-grid--collapsed > [data-size] {
	margin-bottom:0;
	padding:0;
}


/* No vertical margin
----------------------------------------------------------------------------- */
.o-grid--collapsed-v > [data-size] {
	margin-bottom:0;
}


/* Vertical alignment
 * The default is "stretch"
----------------------------------------------------------------------------- */
.o-grid--align-top {
	align-items:flex-start;
}
.o-grid__column--align-top {
	align-self:flex-start;
}
.o-grid--align-middle {
	align-items:center;
}
.o-grid__column--align-middle {
	align-self:center;
}
.o-grid--align-bottom {
	align-items:flex-end;
}
.o-grid__column--align-bottom {
	align-self:flex-end;
}


/* Horizontal alignment
----------------------------------------------------------------------------- */
.o-grid__column--align-right {
	margin-left:auto;
}


/* Auto size
----------------------------------------------------------------------------- */
.o-grid > [data-size="auto"] {
	flex:none;
}


