/* =============================================================================
 * Header
 * postcss-bem-linter: define header
============================================================================= */
.c-header {
	margin-bottom:var(--spacing-4);
	padding:15px 0;
	background-color:#666;
	background-position:50%;
	background-size:cover;
	background-repeat:no-repeat;
}

.c-header__inner {
	display:flex;
	justify-content:space-between;
	align-items:center;
}

.c-header__hero {
	margin-top:100px;
	margin-bottom:80px;
}

.c-header__readmore {
	@mixin text-style-hero-size;
	display:block;
	float:right;
	max-width:90%;
	font-style:italic;
}

@media (--lg) {
	.c-header {
		margin-bottom:115px;
	}
}
