/* =============================================================================
 * Logo
 * postcss-bem-linter: define logo
 *
 * 1. For text fallback if image doesn't load
============================================================================= */
.c-logo {
	font-size:calc(var(--type-size-default) * 1.5); /* [1] */
}

.c-logo__link,
.c-logo__link:visited {
	display:block;
	color:inherit; /* [1] */
	text-decoration:none;
}

.c-logo__image {
	max-width:240px;
}
