/* =============================================================================
 * Forms
============================================================================= */

.s-form label, label.s-form,
.s-form legend, legend.s-form {
	display:block;
	margin-bottom:2px;
	text-transform:none;
}
.s-form label em, label.s-form {
}

.s-form fieldset label {
}

.s-form legend {
	margin-bottom:12px;
}

.s-form .form-controller {
	margin-bottom:24px;
}

.s-form input,		input .s-form,
.s-form select,		select.s-form,
.s-form textarea,	textarea.s-form {
	color:#000;
	font:inherit;
	font-size:var(--type-size-default);
}
.s-form input[type="submit"] {
	color:#fff;
}
.s-form select {
	width:100%;
}
.s-form input:not([type]),		input.s-form:not([type]),
.s-form input[type="text"],		input[type="text"].s-form,
.s-form input[type="email"],	input[type="email"].s-form,
.s-form input[type="tel"],		input[type="tel"].s-form,
.s-form input[type="number"],	input[type="number"].s-from,
.s-form input[type="password"],	input[type="password"].s-form,
.s-form input[type="search"],	input[type="search"].s-form,
.s-form input[type="url"],		input[type="url"].s-form,
.s-form textarea,				textarea.s-form {
	width:100%;
	padding:6px 8px 5px 12px;
	border-color: var(--color-light-gray);
	border-radius:1px;
}
.s-form input:not([type]).error,		input.s-form:not([type]).error,
.s-form input[type="text"].error,		input[type="text"].s-form.error,
.s-form input[type="email"].error,		input[type="email"].s-form.error,
.s-form input[type="tel"].error,		input[type="tel"].s-form.error,
.s-form input[type="number"].error,	input[type="number"].s-from.error,
.s-form input[type="password"].error,	input[type="password"].s-form.error,
.s-form input[type="search"].error,	input[type="search"].s-form.error,
.s-form input[type="url"].error,		input[type="url"].s-form.error,
.s-form textarea.error,				textarea.s-form.error {
	border-color: var(--color-red);
}
.s-form .error-message,
.error-message.s-form {
	color: var(--color-red);
}
.s-form fieldset {
	border:none;
}
.s-form input[type="checkbox"],
.s-form input[type="radio"] {
	margin-bottom:12px;
}
.s-form .form-item {
	margin-bottom:34px;
}
