/* =============================================================================
 * Forms
============================================================================= */

/*
 * 1. Enable line-wrapping in IE8+
 * 2. Enable line-wrapping in other browsers
 */
legend {
	display:table; /* [1] */
	white-space:normal; /* [2] */
}

label {
	display:inline-block;
	margin-bottom:5px;
}

label em {
	color:#767676;
}

button,
input,
select,
textarea {
	color:#000;
	font:inherit;
	font-size:var(--type-size-default);
}

select {
	width:100%;
}

input:not([type]),
input[type="text"],
input[type="url"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="password"],
input[type="search"],
textarea {
	width:100%;
	padding:10px;
	outline:none;
	border:1px solid #000;
	border-radius:0;
	-webkit-appearance:none;
}

input:not([type])[disabled],
input[type="text"][disabled],
input[type="url"][disabled],
input[type="email"][disabled],
input[type="tel"][disabled],
input[type="number"][disabled],
input[type="password"][disabled],
input[type="search"][disabled],
input[type="button"][disabled],
textarea [disabled],
button[disabled] {
	cursor:not-allowed;
}

/*
 * 1. Reset button look in webkit
 */
input[type="reset"],
input[type="button"],
input[type="submit"],
button {
	-webkit-appearance:none; /* [1] */
}

/*
 * 1. Reset search input look in webkit, making it possible to
 *    style it as a normal text input
 */
input[type="search"] {
	-webkit-appearance:none; /* [1] */
}

input::-webkit-search-decoration,
input::-webkit-search-cancel-button {
	display:none; /* [1] */
}

/*
 * 1. Placeholder styling for Gecko 18- (first two rows of selector)
 *    and Gecko 19+ (second two rows of selector)
 */
input:-moz-placeholder,
textarea:-moz-placeholder,
input::-moz-placeholder,
textarea::-moz-placeholder {
	/* [1] */
	color:rgb(128, 128, 128);
	opacity:1;
}

/*
 * 1. Placeholder styling for webkit
 */
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
	color:rgb(128, 128, 128); /* [1] */
}

/*
 * 1. Placeholder styling for IE10
 */
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
	color:rgb(128, 128, 128); /* [1] */
}

/*
 * 1. Reset extra padding on gecko buttons
 */
button::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner {
	/* [1] */
	padding:0;
	border:0;
}

fieldset {
	border:none;
}

legend {
	margin-bottom:10px;
	font-weight:400;
	line-height:20px;
	text-transform:uppercase;
}
